'use client';

import React, { useState, useEffect } from 'react';
import { useForm, type SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { collection, serverTimestamp, addDoc } from 'firebase/firestore';

import { reportSchema } from '@/lib/definitions';
import { useFirebase } from '@/firebase';
import { useToast } from '@/hooks/use-toast';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { AlertTriangle, Clock, File, Loader2, Send } from 'lucide-react';
import { initiateAnonymousSignIn } from '@/firebase/non-blocking-login';
import { errorEmitter } from '@/firebase/error-emitter';
import { FirestorePermissionError } from '@/firebase/errors';

type ReportFormValues = z.infer<typeof reportSchema>;

const fileToBase64 = (file: File): Promise<string> =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result as string);
    reader.onerror = (error) => reject(error);
  });


export function ErrorReportForm() {
  const { toast } = useToast();
  const { auth, firestore, user, isUserLoading } = useFirebase();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formError, setFormError] = useState<string | null>(null);

  const form = useForm<ReportFormValues>({
    resolver: zodResolver(reportSchema),
    defaultValues: {
      clientName: '',
      technicianName: '',
      errorDate: '',
      reportText: '',
      mediaFile: undefined,
      databaseSavedOnPC: "não",
      reportedByUserId: '',
      status: 'open',
    },
  });

  const mediaFile = form.watch('mediaFile');

  useEffect(() => {
    if (!isUserLoading && !user) {
      initiateAnonymousSignIn(auth);
    }
    if (user) {
      form.setValue('reportedByUserId', user.uid);
    }
  }, [user, isUserLoading, auth, form]);


  const onSubmit: SubmitHandler<ReportFormValues> = async (data) => {
    setIsSubmitting(true);
    setFormError(null);

    if (!firestore || !user) {
      const errorMsg = "Autenticação ou serviço de banco de dados não disponível. Aguarde e tente novamente.";
      setFormError(errorMsg);
      setIsSubmitting(false);
      return;
    }
    
    const appId = process.env.NEXT_PUBLIC_FIREBASE_APP_ID;
    if (!appId) {
        setFormError('ID da aplicação Firebase não configurado no ambiente.');
        setIsSubmitting(false);
        return;
    }
    
    let mediaUrl: string | null = null;
    
    try {
        if (data.mediaFile) {
            mediaUrl = await fileToBase64(data.mediaFile);
        }
    } catch (uploadError: any) {
        console.error('Falha na conversão do arquivo para Base64:', uploadError);
        setFormError(`Falha na preparação do arquivo: ${uploadError.message}`);
        setIsSubmitting(false);
        return;
    }


    const reportsCollectionRef = collection(firestore, `artifacts/${appId}/public/data/error_reports`);

    const reportData = {
      clientName: data.clientName,
      technicianName: data.technicianName,
      errorDate: data.errorDate,
      reportText: data.reportText,
      mediaUrl: mediaUrl,
      databaseSavedOnPC: data.databaseSavedOnPC,
      reportedByUserId: user.uid,
      generatedAt: serverTimestamp(),
      status: 'open',
      appId: appId,
    };
      
    addDoc(reportsCollectionRef, reportData)
        .then(() => {
            toast({
                title: 'Sucesso!',
                description: '✅ Relatório de erro enviado com sucesso!',
            });
            form.reset();
        })
        .catch((error) => {
            console.error(error);
            // FirebaseError: Request payload size exceeds the limit: X bytes.
             if (error.code === 'invalid-argument') {
                setFormError('O arquivo enviado é muito grande. O limite total do relatório é de 1MB.');
            } else {
                const permissionError = new FirestorePermissionError({
                    path: reportsCollectionRef.path,
                    operation: 'create',
                    requestResourceData: reportData,
                });
                errorEmitter.emit('permission-error', permissionError);

                setFormError('Falha ao salvar o relatório. Verifique as permissões ou o tamanho dos arquivos.');
            }
        })
        .finally(() => {
            setIsSubmitting(false);
        });
  };
  
  if (isUserLoading) {
    return (
      <div className="flex items-center justify-center p-8">
        <Loader2 className="mr-2 h-6 w-6 animate-spin" />
        <span className="text-lg font-medium">Carregando autenticação...</span>
      </div>
    );
  }

  return (
    <Form {...form}>
      {formError && (
         <Alert variant="destructive" className="mb-4">
            <AlertTriangle className="h-4 w-4" />
            <AlertTitle>Falha no Envio</AlertTitle>
            <AlertDescription>{formError}</AlertDescription>
         </Alert>
      )}
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
        <FormField
            control={form.control}
            name="reportedByUserId"
            render={({ field }) => <Input {...field} type="hidden" />}
        />
        <div className="grid grid-cols-1 gap-6 md:grid-cols-2">
            <FormField
            control={form.control}
            name="clientName"
            render={({ field }) => (
                <FormItem>
                <FormLabel>Nome do Cliente <span className="text-red-500">*</span></FormLabel>
                <FormControl>
                    <Input placeholder="Nome ou ID do cliente afetado" {...field} />
                </FormControl>
                <FormMessage />
                </FormItem>
            )}
            />
            <FormField
            control={form.control}
            name="technicianName"
            render={({ field }) => (
                <FormItem>
                <FormLabel>Nome do Técnico <span className="text-red-500">*</span></FormLabel>
                <FormControl>
                    <Input placeholder="Seu nome completo" {...field} />
                </FormControl>
                <FormMessage />
                </FormItem>
            )}
            />
        </div>
        
        <FormField
          control={form.control}
          name="errorDate"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Data em que o Erro Apareceu <span className="text-red-500">*</span></FormLabel>
              <FormControl>
                <Input type="date" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        
        <FormField
          control={form.control}
          name="mediaFile"
          render={({ field: { onChange, value, ...rest } }) => (
            <FormItem className="pt-4 border-t border-border">
              <FormLabel>Anexo de Mídia (Foto ou Vídeo)</FormLabel>
              <FormControl>
                <Input type="file" accept="image/*,video/*" onChange={(e) => onChange(e.target.files ? e.target.files[0] : undefined)} {...rest}
                className="file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-primary/10 file:text-primary hover:file:bg-primary/20"
                />
              </FormControl>
              {mediaFile && <span className="text-xs text-primary/80 truncate flex items-center"><File className="w-3 h-3 inline mr-1" /> {mediaFile.name}</span>}
              <FormMessage />
            </FormItem>
          )}
        />
        
        <FormField
          control={form.control}
          name="databaseSavedOnPC"
          render={({ field }) => (
            <FormItem className="space-y-3 pt-4 border-t border-border">
              <FormLabel>Banco de dados salvo no PC?</FormLabel>
              <FormControl>
                <RadioGroup
                  onValueChange={field.onChange}
                  defaultValue={field.value}
                  className="flex space-x-4"
                >
                  <FormItem className="flex items-center space-x-2 space-y-0">
                    <FormControl>
                      <RadioGroupItem value="sim" />
                    </FormControl>
                    <FormLabel className="font-normal">
                      Sim
                    </FormLabel>
                  </FormItem>
                  <FormItem className="flex items-center space-x-2 space-y-0">
                    <FormControl>
                      <RadioGroupItem value="não" />
                    </FormControl>
                    <FormLabel className="font-normal">
                      Não
                    </FormLabel>
                  </FormItem>
                </RadioGroup>
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="reportText"
          render={({ field }) => (
            <FormItem className="pt-4 border-t border-border">
              <FormLabel>Relatório Detalhado do Problema <span className="text-red-500">*</span></FormLabel>
              <FormControl>
                <Textarea
                  rows={4}
                  placeholder="Descreva o problema, as etapas para reproduzir e o impacto no cliente."
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        
        <div className="flex items-center justify-between text-sm p-3 rounded-lg border bg-muted/50 text-muted-foreground">
            <div className="font-semibold flex items-center">
              <Clock className="w-5 h-5 mr-2 text-primary" />
              Data de Geração do Relatório:
            </div>
            <div className="text-sm font-mono text-primary/80">
              Gerada automaticamente no envio.
            </div>
        </div>

        <Button type="submit" className="w-full rounded-full" size="lg" disabled={isSubmitting || isUserLoading || !user}>
          {isSubmitting ? (
            <>
              <Loader2 className="mr-2 h-5 w-5 animate-spin" />
              Enviando...
            </>
          ) : (
            <>
              <Send className="mr-2 h-5 w-5" />
              Gerar e Enviar Relatório
            </>
          )}
        </Button>
      </form>
    </Form>
  );
}

    